.home-navbar {
    display: flex;
    box-sizing: border-box;

    width: 100%;
    justify-content: space-between;
    padding-top: 24px;
    padding-bottom: 24px;


    * {
        padding: 0px;
        margin: 0px;
    }

    a {
        text-decoration: none;
        color: black;
    }

    nav {
        ul {
            list-style: none;
            padding: 0px;
            display: flex;
            gap: 16px;
        }
    }
}